import React from "react";

export default function Food(props) {
  console.log("run", props);
  return (
    <React.Fragment>
      <div
        style={({ height: `100px` }, { width: `220px` })}
        className="flex flex-col  bg-white shadow-lg px-3 py-8 rounded-xl"
      >
        <img src={props.link} alt={props.alt} />
        <h1 className="text-2xl capitalize font-semibold text-center pt-3">
          {props.judul}
        </h1>
        <p className="py-3">{props.konten}</p>
        <div className="grid grid-cols-2">
          <p className="text-2xl font-semibold">{props.harga}</p>
          <p className="hover:bg-blue-500 hover:text-white bg-white border border-blue-500 capitalize w-32 p-2 rounded-3xl text-center shadow-md">
            add to cart
          </p>
        </div>
        <button>{props.count}</button>
      </div>
    </React.Fragment>
  );
}
